import React, { Component } from 'react';
import images from './../scripts/imgimporter';

class MyPhoto extends Component{

    constructor() {
        super();
    }
   
    render() {
        return (
            <div className="thumb-item my-photo">
                <div className="thumb-text-container">
                    <div className="thumb-title">{this.props.myPhoto.title}</div>
                    <div className="thumb-desc"  dangerouslySetInnerHTML={{__html: this.props.myPhoto.description}}></div>
                </div>
                <div className="thumb-myphoto">
                    {
                        this.props.myPhoto.images.map(image => <img className={`w${image.width}`} src={ images[image.name]}/>)
                    }
                    
                </div>
            </div>
        );
      }
    
    
    }
    export default MyPhoto;