import React, { Component } from 'react';
import nav from "./scripts/nav.js";
import Navigation from './components/navigation';
import ProjectList from './components/projectlist';
import MyPhotoList from './components/myphotolist';
import Footer from './components/footer';
import ProjectDetail from './components/projectdetail';
import {ProjectDetailList, MyPhotoDetailList} from './scripts/data';

import { library } from '@fortawesome/fontawesome-svg-core'
import {faBehance, faLinkedinIn, faGithub } from '@fortawesome/free-brands-svg-icons'
import { faAngleLeft, faAngleRight, faHourglassHalf, faHome} from '@fortawesome/free-solid-svg-icons'

library.add(
  faBehance, 
  faLinkedinIn, 
  faGithub,
  faAngleLeft,
  faAngleRight,
  faHourglassHalf,
  faHome
)

class App extends Component {
  myPhotos = MyPhotoDetailList;
  details = ProjectDetailList;
  constructor() {
    super();
    this.state = {
      detailIndex: -1,
      showDetail: false,
      isProjectType: true,
    };
    this.showDetail = this.showDetail.bind(this);
    this.stepProject = this.stepProject.bind(this);
    this.openLink = this.openLink.bind(this);
  }

  showDetail = (index, e) => {
    this.setState({
      detailIndex: index,
      showDetail: true,
      isProjectType: true,
    });
  }

  openLink = (url, e) => {
    let win = window.open(url, '_blank');
    win.focus();
  }

  showMyPhotos = () => { 
    this.setState({
      detailIndex: -1,
      showDetail: false,
      isProjectType: false,
    });
  }

  stepProject(step){
    let newIndex = 0;
    let currentIndex = this.state.detailIndex;
    //next
    if(step === 1){
      if(currentIndex === (this.details.length-1)){
        newIndex = -1;
      }
      else{
        newIndex = currentIndex + 1;
      }
    }
    //previous
    else{
      if(currentIndex === 0){
        newIndex = -1;
      }
      else{
        newIndex = currentIndex - 1;
      }
    }

    this.setState({
      detailIndex: newIndex,
      showDetail: newIndex===-1?false:true
    });

  }

  componentDidMount() {
    nav();
  }
  
  render() {
    return (
      <div className="App">
        <Navigation showDetail={ this.showDetail } projects={ this.details } showMyPhotos={this.showMyPhotos}/>
        <div className="wrapper">
        {
          !this.state.isProjectType?<MyPhotoList myPhotos={ this.myPhotos }/> 
          :!this.state.showDetail?
            <div>
                <ProjectList projects={ this.details } myPhotos={ this.myPhotos} showDetail={this.showDetail} projectLink={this.projectLink} openLink={this.openLink} />    
            </div>
          : <ProjectDetail 
                pageInfo={ `${ this.state.detailIndex + 1 } of ${ this.details.length } Projects` }
                detail={ this.details[this.state.detailIndex] }
                showDetail={ this.state.showDetail }
                stepProject={ this.stepProject }
                openLink={ this.openLink }
                isProjectType={ this.state.isProjectType}
            /> 
        }
        </div>
        <Footer />
      </div>
    );
  }
}

export default App;
