      title: "Tic-Tac-Toe Online",
      description: "A multi-player game sharing the same session using ASP.Net's SignalR.",
      techs: ["Angular 5","ASP.net", "C#", "MSSQL", "CSS","Javascript","HTML","SignalR"],
      status: projectStatus.completed,
      image: "https://images.unsplash.com/photo-1506171806810-8b895a54b111?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1267&q=80",
