      demo: null,
      github: 'https://github.com/anthonyolores/tictactoe-multi-player-realtime',
      behance: null,
      sections: [
          {
              title: "Technology Overview",
              text: "<p>It's the typical client-server application with a functionality of long polling. Long polling is used to broadcast the current data that is being changed by the players" +
              " everytime they do a move so that it will reflect to the opposing player. In server side I used microsoft technologies and Angular 2+ in frontend both sides communicate through REST API.</p>",
              image: "p1_0",
              position: 0, //0=column, otherwise=row
              direction: 1, //0=image left-right, otherwise=right-left
              images: []
          },
          {
            title: "Tic-tac-toe",
            text: "<p>Tic-tac-toe (American English), noughts and crosses (British English), or Xs and Os is a paper-and-pencil game for two players, X and O, who take turns marking the spaces in a 3×3 grid. The player who succeeds in placing three of their marks in a horizontal, vertical, or diagonal row wins the game. -Wikipedia</p>",
            image: null,
            position: 1,
            direction: 0,
            images: []
          },
          {
            title: "Step 1: Create a game",
            text: "<p>The player must choose if he wants to create a new game session by clicking the [New] button, otherwise [Join] button to join an already existing game session.</p>",
            image: "p1_1",
            position: 0,
            direction: 1,
            images: []
          },
          {
            title: "Step 2: Choose a game",
            text: "<p>Once the game session has been created a page with empty 3x3 board will display on player 1, the player who created the session will have to wait for an opponent. The player who chose to join a game will see a list of game sessions that they can join. By using a unique identifier per game session players can choose any browser to play the game, as per image, player 1 uses chrome and player 2 is using opera.</p>",
            image: "p1_2",
            position: 0,
            direction: 1,
            images: []
          },
          {
            title: "Step 3: Start the game",
            text: "<p>After player 2 joined player 1's game session, an information will display that a player has joined. Each cell of the board is clickable and count as move of the player. An instruction is displayed underneath the board to which player has the turn to move. In this step it says player 2 will be the first one who will make the move.</p>",
            image: "p1_3",
            position: 0,
            direction: 1,
            images: []
          },
          {
            title: "Step 4: Make a move",
            text: "<p>Everytime a player will make a move it will reflect to opponent's board, and the player's board will be deactived until the opponent will make a move. An instruction will display underneath the board on who's turn is next.</p>",
            image: "p1_4",
            position: 0,
            direction: 1,
            images: []
          },
          {
            title: "Step 5: Game result",
            text: "<p>After both players exchanged their moves and a player hits the winning stroke, a game result popup will show to announce the winner of the game. Once the popup is closed the session will reset and the players can start a new game.</p>",
            image: "p1_5",
            position: 0,
            direction: 1,
            images: []
          },                 
          {
            title: "Conclusion",
            text: "<p>I had fun doing this mini project as it gave me the idea on how to use websocket in providing full-duplex communication channels over a single TCP connection in .Net platform and also I am new to Microsoft's SignalR and was able to make it worked.</p>",
            image: null,
            position: 1,
            direction: 0,
            images: []
          }
      ]           
