import React, { Component } from 'react';
import MyPhoto from './myphoto';
import '../App.scss';

class MyPhotoList extends Component{
constructor() {
    super();
    this.state = {
        imgs: [],
        loadingState: true
    };
    }
    
render() {
    return (
        <div className="thumb-container">
        {
            this.props.myPhotos.map((myPhoto, index) => {
                return <MyPhoto 
                key={index}
                index={index}
                myPhoto={myPhoto}
                />
            })
        }
        </div>
    );
  }


}
export default MyPhotoList;